<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="" prop="regionone">
            <el-select v-model="ruleForm.regionone" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="" value="0.8"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regiontwo">
            <el-select v-model="ruleForm.regiontwo" placeholder="">
                <el-option label="64G" value="1"></el-option>
                <el-option label="256G" value="0.9"></el-option>
                <el-option label="512G" value="0.8"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionthree">
            <el-select v-model="ruleForm.regionthree" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="" value="0.8"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionfour">
            <el-select v-model="ruleForm.regionfour" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="" value="0.8"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionfive">
            <el-select v-model="ruleForm.regionfive" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="/" value="0.9"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionsix">
            <el-select v-model="ruleForm.regionsix" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="/" value="0.8"></el-option>
                <el-option label="/" value="0.7"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionseven">
            <el-select v-model="ruleForm.regionseven" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="" value="0.8"></el-option>
                <el-option label="" value="0.7"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regioneight">
            <el-select v-model="ruleForm.regioneight" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="" value="0.8"></el-option>
                <el-option label="" value="0.7"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    import 'element-ui/lib/theme-chalk/index.css';
    export default {
        data() {
            return {
                ruleForm: {
                    regionone: '',
                    regiontwo: '',
                    regionthree: '',
                    regionfour: '',
                    regionfive: '',
                    regionsix: '',
                    regionseven: '',
                    regioneight: '',
                },
                rules: {
                    regionone: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regiontwo: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regionthree: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regionfour: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regionfive: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regionsix: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regionseven: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    regioneight: [
                        { required: true, message: '', trigger: 'change' }
                    ]

                }
            };
        },
        computed:{
            percent:function () {
                return this.ruleForm.regionone*this.ruleForm.regiontwo*this.ruleForm.regionthree
                    * this.ruleForm.regionfour *this.ruleForm.regionfive*this.ruleForm.regionsix
                    *this.ruleForm.regionseven*this.ruleForm.regioneight
            },
            brandId:function(){
                return this.$store.state.cfselect.truemodel.brandId
            },
            model:function(){
                return this.$store.state.cfselect.truemodel.model
            },
            peakPrice:function () {
                return this.$store.state.cfselect.truemodel.peakPrice
            },
            productsAddress:function () {
                return this.$store.state.cfselect.truemodel.productsAddress
            },
            pageView:function () {
                return this.$store.state.cfselect.truemodel.pageView
            },
            releaseTime:function () {
                return this.$store.state.cfselect.truemodel.releaseTime
            },
            creationTime:function () {
                return this.$store.state.cfselect.truemodel.creationTime
            },
            proId:function () {
                return this.$store.state.cfselect.truemodel.proId
            }
        },

        methods: {

            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.routerjump();
                        console.log("submit");

                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            },
            routerjump(){
                this.$router.push('/eva'+'?'+'percent='+this.percent+'&brandId='+this.brandId+'&model='+this.model+'&peakPrice='
                    +this.peakPrice+'&productsAddress='+this.productsAddress+'&pageView='+this.pageView+'&releaseTime='+this.releaseTime+
                    '&creationTime='+this.creationTime+'&proId='+this.proId)
            },


        }
    }

</script>

<style lang="less">

    </style>